*{
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
  }

  ul{
    list-style: none;
  }

  header{
    padding: 1em;
}

@media all and (max-width: 700px) {
    article {
        grid-column: 1 / 4;
    }
}

.nav__header ul{
    display: flex;
    flex-direction: row;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    background: #eaeaea;
}

.whole__page__grid{
    display: grid;
    grid-template-columns: .5fr 1fr .5fr;
    grid-gap: 1em;
    background: #eaeaea;
    padding-top: 1em;
    min-height: 100vh;
}

#app{
  display: flex;
  height: 100%; 
  justify-content: center;
  align-content: center; 
}
